
import Previewer from 'vux/src/components/previewer/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

import currDatas from '../../../assets/js/data.js'
const aliyunUrl = 'https://345855061.oss-cn-beijing.aliyuncs.com/smallFish/';
const baseList = [{

img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',

title: 'fua'
}, {


img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw1k2wj20p00goq7n.jpg',
title: ''
}, {


title: '',

fallbackImg: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw50iwj20ff0aaaci.jpg'
}];


export default {
components: {
Tabbar,Flow,Swiper,FlowState,FlowLine,XSwitch,Popup ,XTextarea ,GroupTitle,
Datetime ,Previewer ,TabbarItem,XHeader,XButton,XImg,
Tab,Cell ,TabItem,XDialog,Divider,Card ,Group ,XInput ,Timeline, TimelineItem, SwiperItem
},
directives: {
TransferDom
},
mounted() {
this.$store.commit('UPDATE_PAGE_TITLE', '')
},
data(){
return {
demo01_index: 0,
demo01_list: currDatas.idxPhoto,
theProcess: '123',
dialogStyle: {
backgroundColor: 'red',
width: '500px',
height: '800px'
},
photos: currDatas.idxPhoto,
currTabIdx: 0,
popUpShow: false,
photoShow: false,
processDlg: false,
bigImage: false,
bigImageSrc: require('../../../assets/images/smallFish/1.jpg'),
currentPhoto: '',
dialogStyle: {
width: '200px',
height: '200px'
},
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
},
previews: [{
src: 'https://placekitten.com/800/400',
w: 600,
h: 400
},
{
src: 'https://placekitten.com/1200/900',
msrc: 'https://placekitten.com/120/90', // 
w: 1200,
h: 900
}]
}
},
methods:{
nextImage() {
if (this.demo01_index == 2) {
this.demo01_index = 0;
} else {
this.demo01_index = this.demo01_index + 1;
}
},
showBigImage() {
this.bigImage = true;
},
tabClick(idx) {
this.currTabIdx = idx;
},
clickPhoto(idx) {

this.currentPhoto = this.photos[idx];
this.photoShow = true;
},
closePhotoDlg() {
this.photoShow = false;
},
logIndexChange (arg) {
console.log(arg)
},
show (index) {
this.$refs.previewer.show(index)
}
},

}




